.card {
    background: #fff;
    border-radius: 2px;
    height: 125px;
    margin: 0.5rem;
    position: relative;
    width: 125px;
    cursor: pointer;
    padding: 10px;
}

button {
    margin: 20px 0px 0px 0px;
}

button:hover {
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    background-size: cover;
    background-repeat: no-repeat;
    -webkit-box-shadow: 3.5px 3.5px 3.5px 3.5px #888888;
            box-shadow: 3.5px 3.5px 3.5px 3.5px #888888;
}

.card > .img-container {
    height: 100%;
}

.card > .img-container >  img {
    width: 100%;
}


